@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/Lato-Regular.woff2") format("woff2"), url("../fonts/Lato-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/Lato-SemiBold.woff2") format("woff2"), url("../fonts/Lato-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/Lato-Bold.woff2") format("woff2"), url("../fonts/Lato-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/Lato-ExtraBold.woff2") format("woff2"), url("../fonts/Lato-ExtraBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  font-family: "Lato", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.btn-reset {
  padding: 0;
  border: none;
  background-color: rgba(255, 255, 255, 0);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 30px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}

.header {
  z-index: 11;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(16, 16, 29, 0.9);
}
.header.is-active .main-nav__toggle::before {
  top: 15px;
  transform: rotate(-45deg) translate(0, -50%);
}
.header.is-active .main-nav__toggle::after {
  top: 13px;
  transform: rotate(45deg) translate(0, 50%);
}
.header.is-active .main-nav__list {
  transform: translateX(0);
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  padding: 33px 0 32px;
}
@media (max-width: 1023px) {
  .header__wrapper {
    max-width: unset;
  }
}
.header__logo {
  position: relative;
  display: block;
  width: 247px;
  height: 35px;
  margin-right: 40px;
}
@media (max-width: 1023px) {
  .header__logo {
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    z-index: 11;
    width: 165px;
    margin-right: 0;
  }
}
.header__logo svg {
  display: block;
  width: 247px;
  height: 35px;
}
@media (max-width: 767px) {
  .header__logo svg {
    width: 165px;
  }
}

.main-nav__toggle {
  z-index: 11;
  position: relative;
  display: none;
  width: 30px;
  height: 30px;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
@media (max-width: 767px) {
  .main-nav__toggle {
    display: block;
  }
}
.main-nav__toggle::before, .main-nav__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  transition: transform 0.3s ease;
}
.main-nav__toggle::before {
  top: 10px;
}
.main-nav__toggle::after {
  bottom: 10px;
}
@media (hover: hover) {
  .main-nav__toggle:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-nav__toggle.focus-visible:focus {
  opacity: 0.8;
}
.main-nav__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .main-nav__list {
    z-index: 10;
    position: fixed;
    flex-direction: column;
    justify-content: flex-start;
    top: 100px;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 200px 20px 20px;
    background-color: rgba(16, 16, 29, 0.9);
    transition: transform 0.3s ease, visibility 0.3s ease, opacity 0.3s ease;
    transform: translateX(100%);
  }
}
.main-nav__item {
  margin-right: 60px;
}
@media (max-width: 1023px) {
  .main-nav__item {
    margin-right: 30px;
  }
}
@media (max-width: 767px) {
  .main-nav__item {
    margin-right: 0;
    margin-bottom: 30px;
  }
}
.main-nav__item:last-child {
  margin-right: 0;
}
@media (max-width: 767px) {
  .main-nav__item:last-child {
    margin-bottom: 0;
  }
}
.main-nav__link {
  display: block;
  font-weight: 600;
  font-size: 13px;
  line-height: 16px;
  text-transform: uppercase;
  color: #ffffff;
  transition: opacity 0.3s ease;
  transform: translate3d(0, 0, 0);
}
.main-nav__link.is-active {
  color: #43abf0;
  pointer-events: none;
}
@media (hover: hover) {
  .main-nav__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-nav__link.focus-visible:focus {
  opacity: 0.8;
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  padding: 14px 9px;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  text-transform: uppercase;
  border: 1px solid #ffffff;
  border-radius: 4px;
  background-color: #3e9cdc;
  color: #ffffff;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
  outline: none;
}
.btn--white {
  border: 1px solid #6f6d7b;
  background-color: #ffffff;
  color: #2a2b30;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.intro {
  position: relative;
}
.intro__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.intro__background img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.intro__wrapper {
  position: relative;
  padding: 203px 0 203px 335px;
}
@media (max-width: 1439px) {
  .intro__wrapper {
    padding: 203px 0;
  }
}
@media (max-width: 1023px) {
  .intro__wrapper {
    padding: 150px 0;
  }
}
@media (max-width: 767px) {
  .intro__wrapper {
    padding: 130px 0;
  }
}
.intro__title {
  max-width: 780px;
  margin: 0 0 80px;
  font-weight: 700;
  font-size: 48px;
  line-height: 58px;
  text-transform: uppercase;
  color: #000000;
}
@media (max-width: 1023px) {
  .intro__title {
    margin: 0 0 60px;
    font-size: 40px;
    line-height: 50px;
  }
}
@media (max-width: 767px) {
  .intro__title {
    max-width: 450px;
    margin: 0 0 40px;
    font-size: 24px;
    line-height: 34px;
  }
}
.intro__title span {
  color: #3e9cdc;
}
.intro__description {
  margin: 0 0 10px;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #000000;
}
@media (max-width: 767px) {
  .intro__description {
    font-size: 16px;
    line-height: 19px;
  }
}
.intro__list {
  margin: 0 0 80px;
  padding: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .intro__list {
    margin: 0 0 60px;
  }
}
@media (max-width: 767px) {
  .intro__list {
    margin: 0 0 40px;
  }
}
.intro__item {
  position: relative;
  margin-bottom: 5px;
  padding-left: 25px;
}
@media (max-width: 767px) {
  .intro__item {
    padding-left: 15px;
  }
}
.intro__item:last-child {
  margin-bottom: 0;
}
.intro__item::before {
  content: "-";
  position: absolute;
  top: 50%;
  left: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #000000;
  transform: translateY(-50%);
}
@media (max-width: 767px) {
  .intro__item::before {
    font-size: 16px;
    line-height: 19px;
  }
}
.intro__item span {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #000000;
}
@media (max-width: 767px) {
  .intro__item span {
    font-size: 16px;
    line-height: 19px;
  }
}
.intro__buttons {
  display: flex;
  flex-wrap: wrap;
}
.intro__button {
  width: 183px;
  margin-right: 30px;
}
@media (max-width: 767px) {
  .intro__button {
    width: 120px;
    margin-right: 20px;
  }
}
.intro__button:last-child {
  margin-right: 0;
}